*{
  box-sizing: border-box;
  margin:0;
  padding:0
}
#king {
  width: 100%;
  height: 750px;
  background-color: black;
  background-image: url("../Photo/wine.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  color: white;

}

a {
  color: white;
  text-decoration: none;
  font-weight: bold;

}

a:hover {
  color: black;
}

header {

  background: black;
  width: 100%;
  height: 100px;
  top: 0;
  left: 0;
  border-bottom: 2px red solid;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;

}

nav {
  float: right;
  padding: 20px;
}

#menu-icon {


  width: 40px;
  height: 40px;
  background: black url("../img/menu-icon.png");


}

a:hover#menu-icon {

  background-color: red;
  border-radius: 4px 4px 0 0;
}


ul {

  list-style: none;

}

li {

  display: inline-block;
  float: left;
  padding: 10px;

}


  @media only screen and (max-width: 640px) {
    #menu-icon {

      display: block;

    }


  nav ul, nav:active ul {
    display: none;
    position: absolute;
    padding: 20px;
    background: black;
    border: 5px solid #444;
    right: 20px;
    top: 60px;
    width: 50%;
    border-radius: 4px 0 4px 4px;
  }

  nav li {
    text-align: center;
    width: 100%;
    padding: 10px 0;
    margin: 0;
  }

  nav:hover ul {
    display: block;

  }

}

section {
  display: flex;
  justify-content: center;
  align-items: center;

}

